.zoomInRight {
    -webkit-animation-delay:.5s;
            animation-delay:.5s;
}

.zoomInLeft {
    -webkit-animation-delay:1s;
            animation-delay:1s;
}

.fa-arrow-down {
    /* animation-duration:1s; */
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
}

h1{
    font-size:45px;
}

@media (max-width:320px) {
    h1 {
        font-size:35px;
    }
}


