
hr {
    width:80vw;
    display:block;
}




#about {
    width:100%;
    margin: 0 auto;
    /* border-bottom:.25px gray solid; */
    /* box-shadow:0px .25px gray; */
    padding: 10px 0px;
    display:grid;
    grid-template-columns: 50% 50%;
}
#about h3{
    text-decoration:underline;
    font-size:35px;
    margin: auto auto;
    text-align:center;
}
#about p{
    font-size:20px;
    max-width: 40vw;
    margin: auto;
    text-align:left;    
}
#contact {
    padding:0;
    padding:none;
    width:100%;


}
#projects {
    padding: 10px 0px;
    width:100%;
    margin: 0 auto;
    /* border-bottom:.25px gray solid; */
    /* box-shadow:0px .25px gray; */
    /* background-color:rgba(245, 243, 243, 0.904) */
}
hr {
    border: 0;
    height: 3px;
    background-image: -webkit-gradient(linear, left top, right top, from(rgba(0, 0, 0, 0)), color-stop(rgba(0, 0, 0, 0.75)), to(rgba(0, 0, 0, 0)));
    background-image: -webkit-linear-gradient(left, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.75), rgba(0, 0, 0, 0));
    background-image: -o-linear-gradient(left, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.75), rgba(0, 0, 0, 0));
    background-image: linear-gradient(to right, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.75), rgba(0, 0, 0, 0));
    margin-top:0;
}

@media (max-width: 600px) {

    #about {
        display:block;
    }
    #about p {
        margin-left: auto;
        margin-right:auto;
        display:block;
        text-align:left;
        font-size:12px;
        width:500px;
        max-width: 80vw;
    }
    #skills {
        display: block;
        font-size:12px;

    }
    #about div {
        display:block;
    }
}
@media (max-width: 550px) {

}
@media (max-width: 480px) {
    #about p {
        width:400px;
    }
}

@media (max-width: 360px) {
    #about p {
        width:300px;
    }
}


